.@{dialog-prefix-cls} {
  &.mass-action-results {
    & .mass-action-results__list  {
      &,
      & .ps {
        max-height: 300px;
      }
    }

    & .mass-action-results__item {
      align-items: flex-start;
      display: flex;
      flex-direction: row;

      &:not(:last-child) {
        margin-bottom: 8px;
      }
    }

    & .mass-action-results__icon {
      font-size: 16px;
      margin-right: 16px;
      position: relative;
      top: 4px;

      &._success {
        color: @green-6;
      }

      &._fail {
        color: @red-6;
      }
    }
  }
}
